.App {
  text-align: center;
}

html, body {
  background-color: rgb(255, 255, 255);
  margin: 0;   /* This removes any default margin */
  height: 100%; /* Ensures the background covers the entire height */
  width: 100%; /* Ensures the background covers the entire width */
}

