.header {
  display: flex;
  align-items: center;
  padding: 20px;
  background-color: #72b9d8;
  color: white;
  border-radius: 50px;
  justify-content: center;
}

.profile-pic {
  width: 100px;
  height: 110px;
  border-radius: 50%;
  margin-right: 20px;
}

.sections-container {
  display: flex;
  justify-content: space-between;
  gap: 2%;  /* provides space between the two sections */
}

.introduction-section, .funfacts-section {
  box-sizing: border-box;
  padding: 20px;
  border-radius: 50px;  /* rounded corners */
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);  /* subtle shadow for depth */
  
  /* Gradient background for a subtle effect */
  background: linear-gradient(135deg, #f5f7fa, #e4eefe);
  transition: box-shadow 0.3s ease;  /* smooth transition on hover */
}

.introduction-section {
  width: 69%; 
}

.funfacts-section {
  width: 30%; 
}

/* create space between the two sections */
.sections-container {
  gap: 2%;  /* Assuming you're using a flex container for your sections */
}


/* image rescaling */
.introduction-section img {
  max-width: 100%;  /* ensures the image doesn't overflow the container's width */
  height: auto;     /* maintains the image's aspect ratio */
  width: auto;
  display: block;   /* removes any unwanted space below the image */
  margin: 0 auto;   /* centers the image if it's smaller than the container's width */
  border-radius: 40px;
}

.funfacts-section img {
  max-width: 50%;
  height: auto;
  width: auto;
  border-radius: 40px;
}

.introduction-section:hover, .funfacts-section:hover {
  box-shadow: 0 12px 12px rgba(0, 0, 0, 0.15);
}


.introduction-section h2, .funfacts-section h2 {
  font-size: 30px;
  font-family: 'Arial', sans-serif;
  font-weight: bold;
  margin-bottom: 15px;
}

.introduction-section p, .funfacts-section p {
  font-family: 'Arial', sans-serif;
  line-height: 1.5;
  color: #333; 
}

/* bullet points */
.funfacts-section li {
  font-size: 17px;
}




