
/* Button Style */
.button {
    padding: 10px 20px;
    border: none;
    border-radius: 25px;
    font-size: 16px;
    cursor: pointer;
    background: #1e8bd9;
    color: #ffffff;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    margin-top: 1%;
  
    &:hover {
      background: #2c70b1;
      box-shadow: 0 7px 20px rgba(113, 88, 226, 0.25);
    }
  
  }



  